				</router-link>
			</ul>
			<div class="trends" v-show="showTrend">
				<p class="trend-title"><span><img src="./collection-yes.jpg" alt=""></span><span>看车单动态</span></p>
				<ul class="trend-item">
					<li @click="toDetail(trends.vehicle_id)">
						<dl>
							<dt>
								<img v-if="trends.thumbnail != null" width="100%" height="100%" :src="trends.thumbnail" alt="">
								<img v-else width="100%" height="100%" src="../../assets/noImg.jpg" alt="">
								<div class="remark" v-show="trends.commodity_status == 1||trends.commodity_status == 3||trends.commodity_status == 4||trends.commodity_status == 5||trends.commodity_status == 6">
									<p>已售出</p>
								</div>
							</dt>
							<dd>
								<p><span>{{trends.title}}</span></p>
								<p>{{trends.first_register_date | listTime}}/{{trends.mileage}}万公里/{{trends.exhibition_area}}区{{trends.exhibition_code}}</p>
								<p class="carsPrice">
								<span class="amount">{{trends.original_price}}
									<span class="wan">万</span>
								</span>
								<span v-for="tag in trends.tags" class="tag" :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新'}">{{tag}}</span>
								</p>
							</dd>
						</dl>
					</li>
				</ul>
			</div>
		</div>
		<div class="log-off" v-show="logined">
			<span @click="logout">退出登录</span>
