<script>
	import menuPage from "../public/nav.vue";
	import Vue from "vue";
	export default {
		name: 'personal',
		data() {
			return {
				personalMsg: [],
				msgLists: '',
				tokens: '',
				logined: true,
				subscribe: '',
				seeCarCount: '',
				historyCount: '',
				showTrend: false,
				trends: ''
			}
		},
		methods: {
			toOrderList(){
				if(this.logined){
					this.$router.push('./buyCarOrder');
				}else{
					this.$router.push('./login');
				}
			},
			toMsg(){
				if(this.logined){
					this.$router.push('./myMessage');
				}else{
					this.$router.push('./login');
				}
			},
			login() {
				if(this.logined){
					return;
				}else {
					this.$router.push('./login');
				}
			},
			back() {
				this.$router.go(-1)
			},
			showMenu(msg) {
				this.$refs.menuMark.show(msg, $(".selfCenter"));
			},
			toSeeCarList(){
				if(this.logined){
					this.$router.push('./seeCarList');
				}else{
					this.$router.push('./login');
				}
			},
			toHistory(){
				this.$router.push('./seeCarHistory');
			},
			toDetail(vehicle_id){
				this.$router.push('./detail/' + vehicle_id);
			},
			logout() {
				var _this = this;
				_this.storage = window.localStorage;
				$.ajax({
					type: 'POST',
					url: _this.global.host + 'customer/logout',
					data: {
						token: _this.tokens,
					},
					success: function(response) {
						if(response.resultCode == '0000') {
							_this.storage.removeItem('user');
							_this.storage.removeItem('uuid');
							_this.storage.removeItem('knowTime');
							_this.$router.push('./');
						}
					}
				})
			}
		},
		mounted() {
			if(window.localStorage.getItem("user")){
				this.logined = true;
				this.personalMsg = JSON.parse(window.localStorage.getItem("uuid"));
				this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
				var _this = this;
				var seeHistory = window.localStorage.getItem("History");
				$.ajax({
					type: 'POST',
					dataType: 'json',
					url: this.global.host + 'message/getUnreadCount',
					data: {
						token: _this.tokens
					},
					success: function(response) {
						if(response.resultCode == '10086') {
							_this.errorAjax(response.resultMsg, _this)
						} else {
							if(response.resultCode == "0000") {
								_this.msgLists = response.result.unreadCount;
							}
						}
					},
					error: function(response) {

					}
				});
				$.ajax({
					type: 'POST',
					dataType: 'json',
					url: this.global.host + 'customer/getPersonalCenterInfo',
					data: {
						token: _this.tokens
					},
					success: function(response) {
						if(response.resultCode == '10086') {
							_this.errorAjax(response.resultMsg, _this)
						} else {
							if(response.resultCode == "0000") {
								_this.subscribe = response.result.subscriptionCount;
								_this.seeCarCount = response.result.favoriteCarCount;
								_this.historyCount = response.result.historyCount;
								if(response.result.favoriteCarCount > 99){
									_this.seeCarCount = 99 + "+";
								}
							}
						}
					}
				});
				$.ajax({
					type: 'POST',
					dataType: 'json',
					url: _this.global.host + 'inventory/homeRecommend',
					data: {
						locationCity: window.localStorage.getItem("city"),
						token: _this.tokens
					},
					success: function(response) {
						if(response.resultCode == '10086') {
							_this.errorAjax(response.resultMsg, _this)
						} else {
							console.log(response);
							if(response.resultCode == "0000") {
								if(response.result.trend == null){
									_this.showTrend = false;
								}else {
									_this.showTrend = true;
									_this.trends = response.result.trend;
								}
							}
						}
					}
				});
				
		      	if(seeHistory){
		        	_this.historyCount = JSON.parse(seeHistory).length;
		      	}else {
		          	_this.historyCount = 0;
		      	}
			}else {
				console.log(1);
				this.logined = false;
				this.subscribe = 0;
				this.seeCarCount = 0;
				this.historyCount = 0;
			}	
		},
		components: {
			menuPage,
		}
	}
	Vue.filter('listTime', function (value) {//value为13位的时间戳
	    function addt(m) {
	      return m < 10 ? '0' + m : m
	    }
	    var time = new Date(parseInt(value));
	    var y = time.getFullYear();
	    var m = time.getMonth() + 1;
	    var d = time.getDate();

	    return y + '年' + addt(m) + '月';
	});
</script>


